import { Injectable } from "@angular/core";
import { WaldayuInfo } from './global'
import { Entry } from '../pages/shared/entry.model'
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Storage } from '@ionic/storage';

@Injectable()
export class WaldayuService {
    public bookmarks = new BehaviorSubject<Entry[]>([]);
    categories: Object = {};
    constructor(public storage: Storage) {
        if (WaldayuInfo.allAudioEntries.length > 0) {
            this.categories["audio"] = {};
            this.categories["audio"]["audio"] = WaldayuInfo.allAudioEntries;
        }
        for (let key of WaldayuInfo.dataKeys) {
            this.categories[key] = {};
            this.categories[key][key] = WaldayuInfo.dataDict[key].data
        }

    }

    setBookmarks(val) {
        this.bookmarks.next(val)
        this.storage.set(WaldayuInfo.config.L1.name + WaldayuInfo.config.build, JSON.stringify(val));
    }

}


