import { Component } from '@angular/core';

import { NavController, NavParams, ViewController, AlertController, Platform } from 'ionic-angular';

import { File } from '@ionic-native/file';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';

import { Storage } from '@ionic/storage';

import { NativeAudio } from '@ionic-native/native-audio'

import { Entry } from './entry.model'

import { WaldayuInfo } from '../../app/global'

import { WaldayuService } from '../../app/waldayu.service'

@Component({
  selector: 'word-modal',
  templateUrl: 'word-modal.component.html'
})


export class WordModal {
  checkedOptions: string[];
  displayImages: boolean = true; //default show images, turns to false on 404
  entry: Entry
  optional: boolean = false;
  optionalSelection: string[];
  image: string;
  constructor(public navCtrl: NavController,
    private navParams: NavParams,
    public viewCtrl: ViewController,
    public nativeAudio: NativeAudio,
    public alertCtrl: AlertController,
    private file: File,
    private transfer: FileTransfer,
    public storage: Storage,
    public plt: Platform,
    public waldayuService: WaldayuService) {
    this.entry = navParams.get('entry');
    if (this.entry.optional) {
      this.optionalSelection = Object.keys(this.entry.optional)
    }
    this.checkedOptions = this.optionalSelection
    try {
      this.image = 'assets/img/' + this.entry.img[0];
    } catch (error) {
      console.log(error)
    }


  }

  fileTransfer: FileTransferObject = this.transfer.create();
  

  showAlert() {
    let alert = this.alertCtrl.create({
      title: 'Sorry',
      subTitle: 'There is no audio for this yet.',
      buttons: ['Dismiss']
    });
    alert.present();
  };

  showExpAlert() {
    let alert = this.alertCtrl.create({
      title: 'Sorry',
      subTitle: 'There is no audio for this yet. Are you sure you are connected to the internet?',
      buttons: ['Dismiss']
    });
    alert.present();
  };

  audio_playing = [];

  // stopAllAudio() {
  //     for (var i = 0; i < this.audio_playing.length; i++) {
  //         this.audio_playing[i].pause()
  //     }
  //     this.audio_playing = [];
  // }

  playAudio(entry) {
    // this.stopAllAudio()
    // Create Media object from src
    // if (ionic.Platform.platform() == 'macintel') {
    if (this.plt.is('core') || this.plt.is('mobileweb')) {
      if (entry !== undefined && entry.audio_file !== undefined && entry.audio_url !== undefined) {

        let id = entry.entryID
        let path = "http://mobile.firstvoices.com/FirstVoices/" + entry.audio_url[0]
        this.nativeAudio.preloadSimple(id, path).then(this.onSuccess, this.onError);
        this.nativeAudio.play(id).then(this.onSuccess, this.onError);

        // var audio = new Audio(srcURL)
        // audio_playing.push(audio)
        // setTimeout(function () {
        //     audio.play();
        // }, 50);
      } else {
        this.showAlert()
      }
    } else if (this.plt.is('ios')) {

      let id = entry.entryID
      let path = "assets/audio/" + entry.audio_file[0]
      this.nativeAudio.preloadSimple(id, path).then(this.onSuccess, this.onError);
      this.nativeAudio.play(id).then(this.onSuccess, this.onError);
      // srcFN = src.replace(/\//g, '')
      // srcURL = "audio/" + srcFN
      // var audio = new Audio(srcURL);

      // Play audio
      // audio_playing.push(audio)
      // setTimeout(function () {
      //     audio.play();
      // }, 50);

    } else if (this.plt.is('android')) {
      if (entry != undefined) {
        let id = entry.entryID
        let entry_url = "http://mobile.firstvoices.com/FirstVoices/" + entry.audio_url[0]
        let entry_file = "assets/audio/" + entry.audio_file[0]

        this.file.checkFile(this.file.dataDirectory, entry_file)
        .then(_ => {this.nativeAudio.preloadSimple(id, entry_file);
                    this.nativeAudio.play(id);}).catch (err => { var targetPath = this.file.dataDirectory + entry_file;
                                                                 var trustHosts = true;
                                                                 var options = {};
                                                                 this.fileTransfer.download(entry_url, targetPath, trustHosts, options)})
                                                                 .then((entry) => { this.nativeAudio.preloadSimple(id, entry_file);
                                                                                    this.nativeAudio.play(id);}, (error) => {console.log(error)});;}
      } else {
        this.showAlert()
      }
    }

  onSuccess(id) {
    console.log(id)
    // console.log('loaded audio ${id} with path of ${path}'); 
  };

  onError(err) {
    console.log(err)
    let alert = this.alertCtrl.create({
      title: 'Sorry',
      subTitle: "We don't have audio for that entry.",
      buttons: ['OK']
    });
    alert.present();
  };

  dismiss() {
    this.viewCtrl.dismiss();
  }

  showOptions() {

    // Object with options used to create the alert
    let options = {
      title: 'Optional fields',
      message: 'Choose which optional fields to display',
      inputs: [],
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Ok',
          handler: data => {
            let checkedOptions = []
            for (let item of data) {
              for (let key in this.entry.optional) {
                if (this.entry.optional[key] === item) {
                  checkedOptions.push(key)
                }
              }
            }
            console.log(checkedOptions)
            this.checkedOptions = checkedOptions
          }
        }
      ]
    };


    // Now we add the radio buttons
    for (let option of this.optionalSelection) {
      options.inputs.push({ name: 'options', value: this.entry.optional[option], label: option, type: 'checkbox', checked: this.checkChecked(option) });
    }
    let alert = this.alertCtrl.create(options);
    alert.present();

  }

  checkChecked(option) {
    if (this.checkedOptions.indexOf(option) >= 0) {
      return true;
    } else {
      return false;
    }
  }

  imageError() {
    this.displayImages = false;
  }

  toggleFav(entry) {
    let index = WaldayuInfo.dataDict[entry.source_id].data.indexOf(entry)
    if (entry.favourited) {
      WaldayuInfo.dataDict[entry.source_id].data[index].favourited = false;
    } else {
      WaldayuInfo.dataDict[entry.source_id].data[index].favourited = true;
    }
    let favs = []
    for (let source of Object.keys(WaldayuInfo.dataDict)) {
      favs = favs.concat(WaldayuInfo.dataDict[source].data.filter(entry => entry.favourited))
    }
    this.waldayuService.setBookmarks(favs);
  }
}



